import React from "react";

import Backdrop from "../Backdrop/Backdrop";
import Sidebar from "../Sidebar/Sidebar";
import SideDrawer from "../SideDrawer/SideDrawer";
import DrawerCloseButton from "../SideDrawer/DrawerCloseButton";

import "./Projects.css";
import logo from "../../assets/myLogo.png";

export class Projects extends React.Component {
    state = {
        sideDrawerOpen: false
    };

    drawerToggleClickHandler = () => {
        this.setState((prevState) => {
            return {sideDrawerOpen: !prevState.sideDrawerOpen};
        });
    };

    backdropClickHandler = () => {
        this.setState({sideDrawerOpen: false});
    };

    render() {
        let backdrop;

        if (this.state.sideDrawerOpen) {
            backdrop = <Backdrop click={this.backdropClickHandler}/>;
        }
        return (
            <div style={{height: '100%'}}>
                <main>
                    {this.props.children}
                </main>
                <Sidebar drawerClickHandler={this.drawerToggleClickHandler}/>
                <SideDrawer show={this.state.sideDrawerOpen}/>
                <DrawerCloseButton show={this.state.sideDrawerOpen} click={this.backdropClickHandler}/>
                {backdrop}
                <div className={'projects_title'} style={{marginTop: '64px'}}>
                    Projects
                </div>
                <div>
                    <img className={'logo'} src={logo} alt={"Logo"}/>
                </div>
            </div>
        );
    }
}

export default Projects;